import React from 'react';
import s from './Login.module.css';
import { Field, reduxForm } from 'redux-form';
import { Input } from '../common/FormControls/FormControls';
import { required } from '../../utils/validators/validators';
import { connect } from 'react-redux';
import { loginThunk } from './../../redux/auth-reducer';
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';



const LoginForm = (props) => {
    return <div>
        <form onSubmit={props.handleSubmit} className={s.form}>
            <Field component={Input} placeholder="Email..." type="text" name="email" validate={[ required ]}/>
            <Field component={Input} placeholder="Password..." type="text" name="password" validate={[ required ]}/>
            <span>Remember me</span>
            <Field component={Input} type="checkbox" name="rememberMe"/>
            { props.error && <div className={s.form + ' ' + s.error}>{props.error}</div>}
            <div><button>Sign In</button></div>
        </form>
    </div>;
};

const LoginReduxForm = reduxForm({form: "login"})(LoginForm);

const Login = (props) => {
    const onSubmit = (payload) => {
        props.loginThunk(payload.email, payload.password, payload.rememberMe);
    }
    if(props.isAuth) return <Redirect to="/Profile" />
    return <div className={s.content}>
        <div className={s.title}>Login</div>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>;
}
const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    };
};
//Обернуть container component от hoc connect
export default connect(mapStateToProps, { loginThunk })(Login);   