import React from 'react';
import MyPosts from './MyPosts/MyPosts';
import p from './Profile.module.css';
import ProfileUser from './ProfileUser/ProfileUser';
import unknown from './../../assets/userUnknown.jpg';
import AddPostForm from './AddPostForm';

const Profile = (props) => {
    if(props.id)
        return (
            <div className={p.content}>
                    <ProfileUser name={props.profile.fullName} aboutMe={props.profile.aboutMe}
                    photo={props.profile.photos.large !== null ? props.profile.photos.large : unknown}
                    id={props.id}/>
                <div>
                    Работа: 
                    {
                    props.profile.lookingForAJob
                    ? <span>Трудоустроен</span>
                    : <span>Ищу работу</span>
                    }
                    <p>{props.profile.lookingForAJobDescription}</p>
                </div>
                <div>
                    Контакты: 
                    {
                    props.profile.contacts 
                    ? props.contacts.map(
                        c => {
                            return <a className={p.contact} href={"https://" + c}>
                                {c}
                            </a>
                        }
                    ) 
                    : <p>Нет контактов</p>
                    }
                </div>
            </div>
        );
    else {
        let onSubmit = (data) => {
            props.addPost(data.newPostDesc);
        };
        return (
            <div className={p.content}>
            <div className={p.profileInfo}>
                <ProfileUser name={props.profile.fullName} status={props.status}
                updateStatus={props.updateStatus} />
            </div>
                <AddPostForm onSubmit={ onSubmit } />
            <div>
                <MyPosts posts={ props.posts } />
            </div>
        </div>
    );
    }
}


export default Profile;