import { stopSubmit } from 'redux-form';
import { authAPI, usersAPI } from '../api/api';


const SET_AUTH_USER = "SET-AUTH-USER";
const TOOGLE_FETCHING = "TOOGLE-FETCHING";
const SET_ICON = "SET-ICON";

const initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    isFetching: false,
    icon: null
};

const authReducer = (state = initialState, action) => {
    switch(action.type){
        case SET_AUTH_USER:
            return {
                ...state,
                ...action.payload
            };
        case TOOGLE_FETCHING:
            return {
                ...state,
                isFetching: action.value
            };
        case SET_ICON:
            return {
                ...state,
                icon: action.icon
            };
        default:
            return state;
    }
};


const toogleFetching = (value) => ({type: TOOGLE_FETCHING, value});
const setIconUser = (icon) => ({type: SET_ICON, icon})
const setAuthUser = (id, login, email, isAuth) => ({type: SET_AUTH_USER, payload: {id, email, login, isAuth} });

export const getAuthUserData = () => async (dispatch) => {
    dispatch(toogleFetching(true));
    let data = await authAPI.getAuthProfile()
        if(data.resultCode === 0){
            dispatch(setAuthUser(data.data.id, data.data.login, data.data.email, true));
            
            usersAPI.getProfile(data.data.id)
            .then(data => {
                dispatch(setIconUser(data.photos.small));
                dispatch(toogleFetching(false));
            });
            } else {
                dispatch(toogleFetching(false));
            }
};

export const loginThunk = (email, password, rememberMe) => async (dispatch) => {
    dispatch(toogleFetching(true));
    let data = await authAPI.login(email, password, rememberMe = false)
    
        if(data.resultCode === 0){
            let data2 = await authAPI.getAuthProfile()
                if(data2.resultCode === 0){
                    dispatch(setAuthUser(data2.data.id, data2.data.login, data2.data.email, true));
                    dispatch(toogleFetching(false));
                    
                } else {
                    dispatch(toogleFetching(false));
                }
        } else {
            dispatch(stopSubmit("login", { _error: data.messages ? data.messages[0] : "Something wrong!" }))
            dispatch(toogleFetching(false));
        }
};
export const logoutThunk = () => async (dispatch) => {
    dispatch(toogleFetching(true));
    let data = await authAPI.logout()
        if(data.resultCode === 0){
            dispatch(setAuthUser(null, null, null, false));
            dispatch(toogleFetching(false));
        } else {
            dispatch(toogleFetching(false));
        }
};
export default authReducer;