import React, { Component } from 'react'
import './App.css';
import ProfileContainer from './components/Profile/ProfileContainer';
import UsersContainer from './components/Users/UsersContainer';
import Anatomy from './components/Anatomy/Anatomy';
import Nutrition from './components/Nutrition/Nutrition';
import {BrowserRouter, Route} from 'react-router-dom';
import DialogsContainer from './components/Dialogs/DialogsContainer';
import NavbarContainer from './components/Navbar/NavbarContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import { initializingProcess } from './redux/app-reducer';
import PreLoader from './components/common/Loader/PreLoader';
import { connect } from 'react-redux';

  
class App extends Component {
    componentDidMount(){
      this.props.initializingProcess();
    }
    render() {
      if(!this.props.initialized){
        
        return <PreLoader />
      } else
      return (
         
          <BrowserRouter>
          <div className="app-wrapper">
              <HeaderContainer />
              <NavbarContainer />
              <div className="app-wrapper-content">
                <Route exact={true} path='/Profile/:userId?' render={() => <ProfileContainer /> } />
                <Route path='/Dialogs' render={() => <DialogsContainer /> }/>
                <Route path='/Users' render={() => <UsersContainer /> }/>
                <Route path='/Anatomy' component={Anatomy} />
                <Route path='/Nutrition' component={Nutrition} />
                <Route exact path='/login' render={() => <Login /> }/>
      
              </div>
          </div>
          </BrowserRouter>
        );
    }
}
  
  
const mapStateToProps = (state) => ({
    initialized: state.app.initialized  
});

export default connect(mapStateToProps, { initializingProcess })(App);
