import React from 'react';
import p from './ProfileUser.module.css';
import ProfileStatusHooked from '../ProfileStatusHooked';


const ProfileUser = (props) => {
    
    const statusHandler = () => {
        if(props.aboutMe){
            return <p>{props.aboutMe}</p>
        } else {
            return <p>No information</p>
        }
    }
    return (
        <div className={p.contentItem}>
        <div className={p.img}>
                {
                props.photo ? <img src={props.photo} alt="" /> : 
                <img  src="https://image.freepik.com/free-vector/machine-warrior-e-sports-logo-design-machine-warrior-gaming-mascot-twitch-profile_74154-43.jpg" alt=""/>
                }
         </div>         
                
                <div classname={p.status}>
                    Name: <p>{props.name}</p> About me: {!props.status 
                    ? statusHandler()
                    : <ProfileStatusHooked status={props.status} updateStatus={props.updateStatus}/> }
                </div>
                
        </div>
    );
}

export default ProfileUser;