import React from 'react';
import s from './Users.module.css';
import User from './User';


const Users = (props) => {
        let pages = [];
        let pagesCount = Math.ceil(props.usersCount / props.pageSize);
        for (let i = 1; i <= pagesCount; i++)
            pages.push(i);
        
        return <div className={s.content}>

        <div>
            {pages.map(p => {
            return <span key={p} onClick={(e) => {props.onPageChanger(p)}}
            className={props.currentPage === p ? s.selectedPage : s.unselectedPage}>{p}</span>})}
        </div>

        <div>
            {props.users.map(u => {
                //НЕ самый лучший переход на профиль пользователя. Нужны колбеки из пропсов и работа с BLL!
                return <User u={u} {...props}/>
            })}
        </div>
    </div>
};

export default Users;